    text-align: left;
    margin-bottom: 49px;
    padding-left: 0;
    padding-right: 13%; } }

@media (max-width: 767px) {
  .catalog__inner {
    padding: 50px 10px 48px; }
  .catalog__title {
    font-color: 28px;
    line-height: 39px;
    margin-bottom: 42px; }
  .catalog__wrapper {
    padding-left: 4px;
    padding-right: 7px; }
  .catalog__list {
    grid-gap: 30px; }
  .catalog__link span {
    font-color: 16px;
