  .catalog__item {
    max-height: 256px;
    min-width: 283px; } }

.footer {
  position: relative;
  bottom: 0;
  flex-shrink: 0; }

.footer::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  border: 1px solid #626962;
  opacity: 0.2;
  bottom: 40px; }

.footer__inner {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding-top: 59px;
  padding-bottom: 65px;
  max-width: 700px; }

.footer__social {
  display: flex;
  flex-direction: column;
  padding-top: 8px;
  align-items: center;
  max-width: 600px; }

.social__list {
  list-style: none;
  display: grid;
  grid-template-columns: 27px 27px;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  grid-gap: 15px;
  max-height: 27px;
  position: relative;
  left: 41px;
  top: -14px; }

.social__link svg {
  color: #cdcdcd; }

.footer__logo svg {
  color: #ff951be3; }

.social__link svg:hover,
.social__link svg:focus {
  color: #000000; }

.social__date {
  display: none; }

.footer__logo {
  padding-left: 27px; }

.footer__logo-header,
.footer__logo-text {
  margin-top: 0;
  margin-bottom: 0; }

.footer__logowrapper {
  display: flex;
  margin-bottom: 45px; }

.footer__logo-header {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  color: #000000;
  font-color: 25px;
  line-height: 29px;
  padding-top: 5px; }

.footer__logo-text {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 500;
  color: #788891;
  font-color: 12px;
  line-height: 14px; }

.footer__text-main {
  padding-left: 17px; }

.footer__map {
  padding-top: 8px; }

.map__title,
.contacts__title {
  margin-top: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #1b2d37;
  font-color: 15px;
  line-height: 16px;
