.map__title {
  letter-spacing: 0.5px; }

.map__lists {
  display: flex; }

.map__list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0; }

.map__list:not(:last-child) {
  padding-right: 55px; }

.map__item a {
  color: #1b2d37; }

.map__item a,
.contacts__text,
.contacts__link,
