.modal__inner,
.modal-success__inner {
  display: flex;
  width: 490px;
  min-height: 605px;
  position: relative;
  top: calc(50% - 295px);
  right: 0;
  left: 0;
  bottom: auto;
  margin: auto;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  z-index: 2;
  background-color: #ff951be3; }

.modal,
.modal-success {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.8); }

.modal__data {
  display: flex;
  flex-direction: column;
  max-width: 490px;
  padding: 33px 40px 36px; }

.modal__header,
.modal__text {
  margin-top: 0;
  color: #ffffff;
  text-align: left; }

.modal__header {
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-color: 28px;
  line-height: 39px;
  color: #ffffff; }

.modal__text {
  margin-bottom: 51px;
  font-color: 16px;
  line-height: 22px;
  max-width: 380px; }

.modal__form input,
.modal__form textarea {
  padding: 16px;
  margin-bottom: 30px;
  min-width: 380px;
  background-color: #ffb156;
  border: 0;
  color: #ffffff; }

.modal__form textarea {
  recolor: none; }

.modal__form input::placeholder,
.modal__form textarea::placeholder {
  color: #ffffff;
  font-color: 16px;
  line-height: 21px; }

.modal__form button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-color: 14px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: inherit;
  border: 1px solid #ffffff;
  border-radius: 10px;
  padding: 14px 45px;
  cursor: pointer;
  margin-bottom: 34px; }

.modal__form button:hover,
.modal__form button:focus {
  background-color: #ffffff;
  color: #00b2ff; }

.modal-success__title {
  margin: 0 auto;
  display: flex;
  align-items: center;
  max-width: 380px; }

.modal-success__text {
  color: #ffffff;
  font-color: 24px;
  line-height: 31px; }

.modal__close,
.modal-success__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 26px;
  height: 28px;
  border: none;
  background-color: inherit;
  cursor: pointer; }

.modal__checkbox-label {
  max-width: 380px;
  display: flex;
  position: relative;
  min-height: 16px;
  cursor: pointer;
  color: #ffffff;
  font-color: 14px;
  line-height: 16px;
  padding-left: 30px;
  align-items: flex-start;
  text-align: left; }

.modal__checkbox {
  position: absolute;
  appearance: none;
  display: none; }

.modal__checkbox-text {
  position: absolute;
  width: 18px;
  height: 16px;
  left: 0;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat; }

.modal__checkbox:checked + .modal__checkbox-text {
  background-image: url("../img/checkbox-checked.svg");
  background-repeat: no-repeat; }

.modal__lowerpart {
  display: flex;
  flex-direction: column-reverse; }

.hidden {
  display: none; }

.modal input:hover,
.modal textarea:hover,
.modal input:focus,
.modal textarea:focus {
  border: 1px solid #000000; }

.modal input:hover,
.modal input:focus {
  max-height: 50px; }

.modal textarea:hover,
.modal textarea:focus {
  max-height: 142px; }

@media (max-width: 1023px) {
  .modal__inner,
  .modal-success__inner {
    width: 100%;
    justify-content: center; }
  .modal__data {
    width: 300px;
    padding: 10px; }
  .modal__text {
    margin-bottom: 51px;
    font-color: 16px;
    line-height: 22px;
    max-width: 320px; }
  .modal__form input,
  .modal__form textarea {
    min-width: 280px; } }
